export default "import { Component, OnInit } from '@angular/core';\r\nimport { CeuiCheckboxChange } from '@ceui/core/components';\r\nimport { FormControl } from '@angular/forms';\r\n\r\n@Component({\r\n  selector: 'app-basic',\r\n  templateUrl: './basic.component.html',\r\n  styleUrls: ['./basic.component.scss']\r\n})\r\nexport class BasicComponent implements OnInit {\r\n  selected = true;\r\n\r\n  disabled = false;\r\n\r\n  indeterminate = false;\r\n\r\n  control = new FormControl(false);\r\n\r\n  constructor() { }\r\n\r\n  ngOnInit(): void {\r\n  }\r\n\r\n  onChange(event: CeuiCheckboxChange) {\r\n    console.log('change', event);\r\n  }\r\n\r\n  onNgModelChange(value: boolean) {\r\n    console.log('ngModelChange', value);\r\n  }\r\n\r\n  onGet() {\r\n    console.log(this.selected);\r\n    console.log(this.control.value);\r\n  }\r\n\r\n  onSet() {\r\n    this.selected = !this.selected;\r\n    this.control.setValue(!this.control.value);\r\n  }\r\n\r\n  onDisabled() {\r\n    this.disabled = true;\r\n    this.control.disable();\r\n  }\r\n\r\n  onEnabled() {\r\n    this.disabled = false;\r\n    this.control.enable();\r\n  }\r\n\r\n  onIndeterminate() {\r\n    this.indeterminate = true;\r\n  }\r\n}\r\n";